@import url(https://fonts.googleapis.com/css2?family=Lexend&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300&display=swap);
/* base styles */
* {
  margin: 0;
  font-family: "Lexend";  
}

.App-logo {
  height: 8vmin;
  pointer-events: none;
  border-radius:100%;
  padding-inline: 2vmin;
}

.home {
  padding: 10px;
  -webkit-align-items: center;
          align-items: center;
  max-width: 75%;
  margin: 0 auto;
  font-size: smaller;
  letter-spacing: .05vh;
}

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
  background-color: #f8f8f8;
  color: #333;
  border-radius: 7px;
}
.blog-preview:nth-child(3) {
  background: black;
  color: wheat;
}
.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.navbar {
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 75%;
  margin: 0 auto;
  border-bottom: 3px solid #f2f2f2;
}
.navbar h1 {
  color: #16325c;
  font-family: 'Kalam';  
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: #333;
}
.navbar a:hover {
  color: #16325c;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
